// Inside your npm package (ProfileService)
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class ProfileUpdateService {
  private profilePictureSubject = new BehaviorSubject<string>('/path/to/default-profile.jpg');

  profilePicture$: Observable<string> = this.profilePictureSubject.asObservable();

  updateProfilePicture(newPicturePath: string) {
    this.profilePictureSubject.next(newPicturePath);
  }
}
